html, body {
  height: 100%;
  width: 100%;
}
body {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
body.fontLoaded {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
#app {
  min-height: 100%;
  min-width: 100%;
}
p, label {
  font-family: Georgia, Times, 'Times New Roman', serif;
  line-height: 1.5em;
}

.homePage {
  margin-left: 40px;
  margin-right: 20px;
  margin-bottom: 20px;
  max-height: 600px;
  overflow-y: scroll;
}
.homePage input {
  margin-top: 5px;
  max-width: 30%;
}
.homePage img {
  width: 250px;
  height: 250px;
}

.equilateralTriangle img {
  width:200px;
  height:200px;
}
.isoscelesTriangle img {
  width:200px;
  height:200px;
}
.scaleneTriangle img {
  width:200px;
  height:200px;
}
.not-found-page {
  h1 {
    font-size: 2em;
    margin-bottom: 0.25em;
  }
}
.footer-distributed {
	background-color: black;
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
	box-sizing: border-box;
	width: 100%;
	text-align: left;
	font: normal 16px sans-serif;
	padding: 45px 50px;
	margin-top: 80px;
  }
  .footer-distributed .footer-left p {
	color: white;
	font-size: 14px;
	margin: 0;
  }
  /* Footer links */
  .footer-distributed p.footer-links {
	font-size: 18px;
	font-weight: bold;
	color: #fff;
	margin: 0 0 10px;
	padding: 0;
  }
  .footer-distributed p.footer-links a {
	display: inline-block;
	line-height: 1.8;
	text-decoration: none;
	color: inherit;
  }
  .footer-distributed .footer-right {
	float: right;
	margin-top: 6px;
	max-width: 180px;
  }
  .footer-distributed .footer-right a {
	display: inline-block;
	width: 35px;
	height: 35px;
	background-color: #33383b;
	border-radius: 2px;
	font-size: 20px;
	color: #fff;
	text-align: center;
	line-height: 35px;
	margin-left: 3px;
  }
  /* If you don't want the footer to be responsive, remove these media queries */
  @media (max-width: 600px) {
	.footer-distributed .footer-left, .footer-distributed .footer-right {
	  text-align: center;
	}
	.footer-distributed .footer-right {
	  float: none;
	  margin: 0 auto 20px;
	}
	.footer-distributed .footer-left p.footer-links {
	  line-height: 1.8;
	}
  }
  
footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}

